:root {
    --color_1: #e3edf2;
    --color_2: #e85c5c;
    --color_3: #e3edf2;
    --color_4: #ffffff;
    --color_5: #ffffff;

    --light_font: #e3edf2;
    --dark_font: #161e2a;
}

body {
    font-size: small;
    background-color: var(--color_1);
    padding: 0%;
    margin: 0%;
}

#selections_header {
    font-size: medium;
    background-color: var(--color_2);
    color: var(--dark_font);
}

.custom-select {
    font-size: small;
    color: var(--dark_font);
}

table {
    font-size: small;
    /*line-height: 10px;*/
}

/* form-control */

#middle_table tr td {
    line-height: 5px;
    height: 5px;
}

#middle_table_2 tr td {
    line-height: 5px;
    height: 5px;
}

#left_column {
    font-size: small;
    font-weight: bold;

    background-color: var(--color_1);
    color: var(--dark_font);

    border-style: solid;
    border-color: var(--color_5);
    border-width: 5px;
}

#middle_column {
    background-color: var(--color_1);
    color: var(--color_1);

    align-items: center;
    align-content: center;

    font-weight: bold;
    border-style: solid;
    border-color: var(--color_5);;
    border-width: 5px;
}

