img {
    width: 85%;
    height: 100%;
    object-fit: cover;

    background-color: white;

    margin: 0%;
    padding: 0%;

    border-style: solid;
    border-color: var(--dark_font);
    border-width: 1px;
}

.center {
    display: block;
    margin-left: auto;
    margin-right: auto;
}

#right_column {
    background-color: var(--color_1);

    border-style: solid;
    border-color: var(--color_5);
    border-width: 5px;
}

#elements_header {
    font-size: medium;
    font-weight: bold;
    background-color: var(--color_2);
    color: var(--dark_font);
}


.myBox {
    font-size: 1.3em;
    height: 1.6em;
}


